#about {
  height: 100%;
  width: 100%;
  background: white;
}

.fixed-nav {
  position: fixed;
  top:0;
  z-index: 999;
}

.title-underline {
  height: 4px;
  width: 100px;
  background-color: black;
  margin: 10px auto 40px auto;
}

.fa-5x {
  font-size: 7rem;
}

.icon-tag {
  margin: 1rem auto 0.1rem auto;
  font-size: 1.4rem;
  font-weight: 700;
}

.icon-text {
  color: rgb(110, 110, 110);
  font-size: 1rem;
  font-weight: 500;
  width: 70%;
  margin: 0 auto;
}

/* Second row of about */

.final-about {
  margin: 8% auto;
}

.image-section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.icon-text {
  color: rgb(110, 110, 110);
  font-size: 1rem;
  font-weight: 500;
  width: 90%;
  margin: 0 auto;
}

.css-svg {
  height: 90px;
}

.ruby {
  height: 75px;
}
/* End of normal second row */

/* Start of experimental skills section */

.button {
  border: none;
  margin: 0 auto;
  width: 20%;
  background: rgb(62, 147, 175);
  font-size: 22px;
  margin-top: 30px;
  margin-bottom: 30px;
  cursor: pointer;
  display: flex;
  align-self: center;
  min-width: 100px;
  /* Need to position the button relatively - maybe change it's alignment through flexbox */
}

.button-text {
  color: rgb(255, 255, 255);
}

.skills-section {
  display: flex;
  flex-direction: column;
  font-size: 1.5rem;
  font-weight: 700;
  position: relative;
}

.marg-upper {
  margin-top: 100px;
}

.index-one {
  position: relative;
  z-index: 1;
}

.index-two {
  position: relative;
  z-index: 2;
}

.svg {
  width: 140px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.first-svg {
  position: relative;
  align-self: center;
  top: 12%;
}

.second-svg {
  position: relative;
  top: 25%;
  align-self: flex-start;
}

.third-svg {
  position: relative;
  align-self: flex-end;
  top: 6%;
  height: 110px;
}


.phpSVG {
  width: 150px;
}

.rubySVG {
  top: 6%;
}

