
html, body {
  font-family: 'Raleway', sans-serif!important;
}

h1 {
  padding: 0;
  margin: 0;
}

body {
  padding: 0px;
  margin: 0px;
  background-color: #000000;
  text-align: center;
}

canvas {
  width: 100%;
  height: 100%;
  min-height:100%;
}

/* Lets see */
.particles {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background-color: #b61924; */
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  z-index: 1;
}

.home-text {
  position:  relative;
  height: 100vh;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.under-text {
  position: relative;
  z-index: -1;
}

.home-text h1 {
  font-size: 2.8rem;
}

.homebutton {
  position:  relative;
  margin-top: 20px;
  border: 2px solid white;
  font-size: 1.5rem;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  background: #1b1d25;
  min-width: 210px;
  width: 235px;
  z-index: 10;
}

.font-arrow {
  margin-left: 18px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.homebutton:hover .font-arrow {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg)
}

.homebutton:hover {
  background: rgb(62, 147, 175);
  border: 2px solid rgb(62, 147, 175);
}
.homebutton a:hover {
  text-decoration: none;
}

.button-wrapper {
  padding: 6px 20px;
  color: #fff;
}

.span-text {
  color: #c51515;
}

/* Miscellaneous bits */ 

.title-underline {
  height: 4px;
  width: 100px;
  background-color: black;
  margin: 10px auto 40px auto;
}

.title-wrapper {
  margin: 80px 0 30px 0;
}

.title-wrapper h1 {
  font-weight: 600;
}
nav {
  width: 100%;
  margin: 0;
  padding: 0!important;
  top:0;
}

.fixed-nav {
  position: fixed;
  top: 0!important;
}

.navbar {
  color: rgba(255, 255, 255, 0.705);
  font-size: 1.1rem;
  font-weight: 400;
  display: flex;
  border-top: 2px solid rgb(62, 147, 175);
  border-bottom: 3px solid rgb(62, 147, 175);
  position: -webkit-sticky;
  position: sticky;
  background: #1b1d25;
  z-index: 100;
}

.navbar ul {
  display: inline-flex;
  margin-bottom: 0;
  justify-content: space-between;
  list-style: none;
  margin: 10px 1%;
}

.navbar ul li {
  padding-right: 30px;
  padding-left: 30px;
}
.navbar ul a {
  color: white;
  font-weight: 600;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.navbar ul a:hover {
  text-decoration: none;
  color: rgb(209, 53, 53);
}
