}


#projects {
  background: #f5f5f5f5;
  padding-bottom: 20vh;
}

.project-wrapper {
  background: grey;
  margin: 0.5%;
  height: 35vh;
  min-width: 300px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.project-wrapper p {
  margin: 0;
  padding: 0;
}

/* Inside project card */
.project-title {
  font-weight: 700;
  font-size: 1.2rem;
  position: absolute;
  top: 0;
  opacity: 1;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.project-text {
  font-size: 1rem;
  position: absolute;
  top: 12%;
  opacity: 1;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  color: #c51515;
}

.project-button {
  background: darkgrey;
  width: 40%;
  height: 17%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 0;
  opacity: 1;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  z-index: 10;
}

.hover-wrapper a {
  display: flex;
  justify-content: center;
  color: black;
}

.hover-wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  background: white;
  opacity: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

/* On project card hover */

.project-wrapper:hover .hover-wrapper {
  opacity: 0.98;
}

.project-wrapper:hover .project-title {
  opacity: 1;
  -webkit-transform: translate(0px, 55px);
          transform: translate(0px, 55px);
}

.project-wrapper:hover .project-text {
  opacity: 1;
  -webkit-transform: translate(0px, 60px);
          transform: translate(0px, 60px);
}

.project-wrapper:hover .project-button {
  opacity: 1;
  -webkit-transform: translate(0px, -50px);
          transform: translate(0px, -50px);
}

.project-wrapper:hover .project-button:hover {
  background: rgb(78, 185, 221);
  cursor: pointer;
  color: white;
}

/* End project card hover */

